.menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #175a85;
  z-index: 10000;
  text-align: right;
  padding-top: 2vh;
  overflow-y: auto;
  transform: translateX(-100%); /* התפריט מוסתר מההתחלה */
  transition: transform 0.4s ease-in-out; /* מעבר חלק */
}

.menu.open {
  transform: translateX(0); /* הזזת התפריט למקומו */
}


.menu.close {
  transform: translateX(-100%); /* החזרת התפריט לצד שמאל */
}

@media screen and (max-width: 768px) {

  .menu-title {
    font-size: 8vw;
    color: white;
    margin-bottom: 5vh;
    margin-top: 19vh;
    margin-right: 4vw;
    text-align: center;
    padding: 5vw;
    font-size: 3em;
    font-family: "Karantina";
  }
  
  #last-item-m{

    margin-bottom: 8vh;
  }
  
  
  .menu-list {
    list-style-type: none;
    padding: 0;
    margin-top: 2vh;
  }
  
  .menu-list li {
    color: white;
    margin-bottom: 3vh;
    padding: 1.5vh 0;
    text-align: center;
    font-size: 1.3em;
    font-family: "Heebo";
  }
  
  .lineMenu{
    border-bottom: 0.2vh solid white;
    width: 86vw;
    position: absolute;
    right: 50%;
    transform: translateX(50%);
    margin-top: -2vh;
  }
  
  #lineMenuLast{
    margin-top: -7vh;
  }
  .tzahi-white {
    position: absolute;
    top: 2vh;
    left: 2vw;
    width: 15%;
    height: auto; 
    z-index: 100000;
}
  
  .closeBtn {
    position: absolute;
    top: 2vh;
    left: 2vw;
    width: 15%;
    height: auto;
    z-index: 100000;
}

.menu.open .closeBtn {
    opacity: 1;
}
  
  .menu-list li.active {
    background-color: white;
    color: #175a85;
  }
  
  
  .mashov-menu{
    font-family: 'Heebo', sans-serif;
    font-size: 4vw;
    color: white;
    text-align: center;
    position: absolute;
    margin-top: 0vh;
    right: 50%;
    transform: translateX(50%);
    width: 67vw;
  }
  
  .mashovTextMenu{
    margin-bottom: 6vh;
    font-family: "Heebo";
    text-align: center;
    font-size: 1.1em;
  }
  
  #linkMenu{
    color: white;
  }
  .whiteLogoHam{
    width: 30%;
    height: auto;
    position: absolute;
    top: 7vh;
    transform: translateX(50%);
    right: 50%
  }

  .tzahi-white{
    position: fixed; 
    top: 2.5vh;
    left: 5vw;
    width: 13vw; 
    height: auto;
    z-index: 10; 
  }

  .linkCreditsWhite{
    position: absolute;
    text-align: right;
    direction: rtl;
    transform: translateX(50%);
    right: 50%;
    text-decoration: underline;
    margin-top: 12vh;
  }
}
